<template>
	<div id="activity-list">
		<public-header title="商户活动"></public-header>
		<div class="content">
			<div class="wrap-search wrap-search-input">
				<div class="tab-group flex-lr border-1px-b">
					<div class="tab-item" :class="{active: filter.items.status == -1}" @click="onToggleStatus(-1)">全部</div>
					<div class="tab-item" :class="{active: filter.items.status == 1}" @click="onToggleStatus(1)">进行中</div>
					<div class="tab-item" :class="{active: filter.items.status == 2}" @click="onToggleStatus(2)">待生效</div>
					<div class="tab-item" :class="{active: filter.items.status == 0}" @click="onToggleStatus(0)">已结束</div>
				</div>
				<van-search class="padding-10" placeholder="请输入店铺名称" v-model="filter.items.keyword" />
			</div>
			<van-pull-refresh v-model="isRefresh" @refresh="onPullDownRefresh()">
			<van-list
					v-model="records.loading"
					:finished="records.finished"
					@load="onLoad"
					:offset="100"
					:immediate-check="false"
					v-if="!records.empty"
					class="actitity-list"
			>
			<div class="activity-item margin-10-b" v-for="(item, index) in records.data" :key="item.id">
				<div class="padding-10">
					<div class="flex-lr">
						<div class="flex">
							<div class="store-name">{{item.store_title}}</div>
							<div class="activity-type itag itag-primary">{{item.type_cn}}</div>
						</div>
						<div class="itag itag-primary" v-if="item.status == 1">进行中</div>
						<div class="itag itag-danger" v-else-if="item.status == 2">待生效</div>
						<div class="itag itag-disabled" v-else-if="item.status == 0">已结束</div>
					</div>
					<div class="padding-10-t">
						<div class="activities c-gray">活动内容：<span>{{item.title}}</span></div>
						<div class="font-15 c-gray">活动时间：<span>{{item.starttime_cn}}~{{item.endtime_cn}}</span></div>
					</div>
				</div>
				<div class="btn-group border-1px-t">
<!--
					<router-link tag="div" class="btn-item bg-primary" :to="util.getUrl({path: '/pages/merchant/activity/post', query:{id: item.id}})">编辑</router-link>
-->
					<div class="btn-item" @click="onToggleActivity(item, index)">撤销</div>
				</div>
			</div>
			<div class="loaded" v-if="records.finished">
				<div class="loaded-tips">没有更多了</div>
			</div>
			</van-list>
			<div class="no-data" v-else>
				<img src="static/img/order_no_con.png" alt="">
				<p>暂无商户活动记录!</p>
			</div>
			</van-pull-refresh>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import {getRecords} from "@/controller/funcCommon"
	export default {
		data() {
			return {
				records:{
					page: 1,
					psize: 15,
					loading: false,
					finished: false,
					empty: false,
					data:[]
				},
				showPreLoading:true,
				isRefresh: false,
				filter: {
					items: {
						status: '-1'
					}
				}
			}
		},
		components: {
			publicHeader,
		},
		methods: {
			onLoad(force){
				getRecords({
					vue: this,
					force: force,
					url: 'plateform/merchant/activity/list'
				});
			},
			onPullDownRefresh(){
				this.onLoad(true);
			},
			onToggleActivity(item, index){
				var that = this;
				that.util.jspost({
					url: 'plateform/merchant/activity/del',
					data: {
						id: item.id
					},
					confirm: '确定撤销商户活动吗？',
					success: function(){
						that.records.data.splice(index, 1);
					}
				});
			},
			onToggleStatus(value){
				value = parseInt(value);
				if(this.filter.items.status == value) {
					return;
				}
				this.filter.items.status = value;
			}
		},
		mounted(){
			this.onLoad();
		},
		watch: {
			filter: {
				handler(newValue, oldValue) {
					this.onLoad(true)
				},
				deep: true
			}
		}
	}
</script>

<style>
	#activity-list .content .activity-item{
		background-color: #fff;
	}
	#activity-list .content .activity-item .store-name{
		font-size: 18px;
		padding-right: 10px;
		font-weight: bold;
		line-height: 30px;
	}
	#activity-list .content .activity-item .activity-type{
		padding: 2px 4px;
		font-size: 12px;
	}
	#activity-list .content .activity-item .activities{
		font-size: 15px;
		padding-bottom: 5px;
	}
	#activity-list .content .activity-item .btn-group{
		display: flex;
		flex-wrap: nowrap;
		justify-content: flex-end;
		align-items: center;
		padding: 10px;
	}
	#activity-list .content .activity-item .btn-group .btn-item{
		min-width: 60px;
		margin-left: 10px;
		text-align: center;
		padding: 10px 8px;
		border: 1px solid #e5e5e5;
		border-radius: 3px;
		font-size: 14px;
	}
</style>


